export const MON_LIST_LOADING = 'MON_LIST_LOADING';
export const MON_LIST_LOADED = 'MON_LIST_LOADED';
export const MON_LIST_ERROR = 'MON_LIST_ERROR';

export function monListLoading() {
  return {
    type: MON_LIST_LOADING
  };
}

export function monListLoaded(monList) {
  return {
    type: MON_LIST_LOADED,
    payload: monList
  };
}

export function monListError(error) {
  return {
    type: MON_LIST_ERROR,
    payload: error
  };
}
