import * as React from "react";
import ViewPort from "./ViewPort";
import { handleKeyDown } from "./animation";
import "./App.css";

export function reloadGame() {
  window.location.reload();
}

function App() {
  const [moving, setMoving] = React.useState(false);
  document.onkeydown = (ev: KeyboardEvent) =>
    handleKeyDown(ev, setMoving, !moving);

  return (
    <>
      <header style={styles.header}>
        <h1>The Jude Game</h1>
      </header>
      <main>
        <ViewPort moving={moving} />
      </main>
      <footer style={styles.footer}>
        <button style={styles.button} onClick={reloadGame}>
          Reload
        </button>
      </footer>
    </>
  );
}

const styles = {
  header: { textAlign: "center" },
  footer: { textAlign: "center", padding: 32 },
  button: { fontSize: "2.5em" }
};

export default App;
