import React from 'react';
import { PlayerAction } from './types';
import standing from './img/jude.png';
import running from './img/jude.gif';

const ACTION_MAP = new Map([
  [PlayerAction.Standing, standing],
  [PlayerAction.Moving, running]
]);

interface Props {
  moving: boolean;
}

function getSprite(action: PlayerAction) {
  return ACTION_MAP.get(action) || standing;
}

export default (props: Props) => {
  const action = props.moving ? PlayerAction.Moving : PlayerAction.Standing;

  return (
    <div id="player" style={styles.sprite}>
      <img src={getSprite(action)} width="120" alt="sprite" />
    </div>
  );
};

const styles = {
  sprite: {
    position: 'absolute',
    top: 365,
    // bottom: 150,
    left: 128
  }
};
