import {
  Box,
  Polygon,
  Response as SATResponse,
  Vector,
  testPolygonPolygon
} from "sat";
import idx from "idx";
import { MIN_VIEW_WIDTH } from "./constants";
import { reloadGame } from "./App";

let playerAnimation: Animation;
let dartAnimation: Animation;
const DART_DURATION = 500;

