/*** Player ***/
export function jump() {
  const player = document.getElementById("player");
  if (!player) return;

  playerAnimation = player.animate(
    [
      { transform: "translateY(0px)", easing: "ease-out" },
      { transform: "translateY(-350px)", easing: "ease-in" },
      { transform: "translateY(0px)" }
    ],
    {
      duration: 700
    }
  );
}

export function shoot(dart: HTMLElement) {
  // Baddies
  const hydra =
    document.getElementById("hydra") || document.createElement("div");
  const stal = document.getElementById("stal") || document.createElement("div");
  const spider =
    document.getElementById("spider") || document.createElement("div");
  const zombie =
    document.getElementById("zombie") || document.createElement("div");
  const madFace =
    document.getElementById("mad-face") || document.createElement("div");
  const scrapFace =
    document.getElementById("scrap-face") || document.createElement("div");

  // Dart
  dartAnimation = dart.animate(
    [
      { transform: "translateX(0px)" },
      { transform: `translateX(${MIN_VIEW_WIDTH}px)` }
    ],
    {
      duration: DART_DURATION
    }
  );

  // Colliders
  const {
    height: dh,
    width: dw,
    left: dx,
    top: dy
  } = dart.getBoundingClientRect();
  const {
    height: sh,
    width: sw,
    left: sx,
    top: sy
  } = stal.getBoundingClientRect();
  const {
    height: sph,
    width: spw,
    left: spx,
    top: spy
  } = spider.getBoundingClientRect();
  const {
    height: zh,
    width: zw,
    left: zx,
    top: zy
  } = zombie.getBoundingClientRect();
  const {
    height: hh,
    width: hw,
    left: hx,
    top: hy
  } = hydra.getBoundingClientRect();
  const {
    height: mfh,
    width: mfw,
    left: mfx,
    top: mfy
  } = madFace.getBoundingClientRect();
  const {
    height: sfh,
    width: sfw,
    left: sfx,
    top: sfy
  } = scrapFace.getBoundingClientRect();

  // Collision detection
  detectCollision(
    { h: dh, w: dw, x: dx, y: dy },
    dart,
    { h: sh, w: sw, x: sx, y: sy },
    stal
  );
  detectCollision(
    { h: dh, w: dw, x: dx, y: dy },
    dart,
    { h: sph, w: spw, x: spx, y: spy },
    spider
  );
  detectCollision(
    { h: dh, w: dw, x: dx, y: dy },
    dart,
    { h: zh, w: zw, x: zx, y: zy },
    zombie
  );
  detectCollision(
    { h: dh, w: dw, x: dx, y: dy },
    dart,
    { h: hh, w: hw, x: hx, y: hy },
    hydra
  );
  detectCollision(
    { h: dh, w: dw, x: dx, y: dy },
    dart,
    { h: mfh, w: mfw, x: mfx, y: mfy },
    madFace
  );
  detectCollision(
    { h: dh, w: dw, x: dx, y: dy },
    dart,
    { h: sfh, w: sfw, x: sfx, y: sfy },
    scrapFace
  );
}

export function handleClick(ev: MouseEvent) {
  const player = document.getElementById("player");
  if (!player) return;

  const dart = document.createElement("div");

  // 200, 232
  dart.id = "dart";
  dart.style.visibility = "hidden";
  dart.innerHTML = '<img src="img/dart.png" width="16" alt="dart" />';

  const res = player.insertAdjacentElement("afterend", dart) as HTMLElement;
  if (!res) return;

  let top, left;
  // const topOffset = 58;
  const leftOffset = 104;
  if (playerAnimation && playerAnimation.playState === "running") {
    const rect = idx(playerAnimation, _ =>
      _.effect.target.getBoundingClientRect()
    );
    top = rect.top - 38;
    left = rect.left - 9;
  } else {
    top = parseInt(player.style.top || "0") + 58;
    left = parseInt(player.style.left || "0");
  }

  res.style.position = "absolute";
  res.style.left = left + leftOffset + "px";
  res.style.top = top + "px";
  res.style.visibility = "visible";

  shoot(res);
}
/*** end Player ***/

/*** NPCs */
export function animateNpc(target: any) {
  if (!target) return;

  const anim = target.animate([], { duration: 1 });
}
/*** end NPCs */

/*** Global ***/
export function handleKeyDown(
  ev: KeyboardEvent,
  stateHook?: any,
  stateHookArg?: any
): any {
  switch (ev.code) {
    case "Enter":
      stateHook(stateHookArg);
      break;
    case "Space":
      jump();
      break;
    case "KeyR":
      reloadGame();
      break;
    default:
      break;
  }
}

export function detectCollision(
  firstElement: any,
  firstElRef: any,
  secondElement: any,
  secondElRef: any
) {
  const box1 = new Box(
    new Vector(firstElement.x, firstElement.y),
    firstElement.h,
    firstElement.w
  ).toPolygon();
  let box2: Polygon = new Box(
    new Vector(secondElement.x, secondElement.y),
    secondElement.h,
    secondElement.w
  ).toPolygon();

  const response = new SATResponse();
  let collided: boolean;
  let start: number = -1;

  function step(ts: number) {
    if (start < 0) start = ts;
    const progress = ts - start;

    collided = testPolygonPolygon(box1, box2, response);
    if (collided && !secondElRef.getAttribute("data-status")) {
      firstElRef.remove();
      secondElRef.setAttribute("data-status", "dead");
      killShot(secondElRef);
      return;
    }

    const { x: currX, y: currY } = firstElRef.getBoundingClientRect();
    const { x: prevX, y: prevY } = box1.pos;
    box1.setOffset(
      calculateDelta(
        Math.round(prevX),
        Math.round(prevY),
        Math.round(currX),
        Math.round(currY)
      )
    );

    if (progress < DART_DURATION) {
      window.requestAnimationFrame(step);
    }
  }

  dartAnimation.onfinish = () => {
    firstElRef.remove();
  };

  window.requestAnimationFrame(step);
}

function calculateDelta(
  prevX: number,
  prevY: number,
  currX: number,
  currY: number
) {
  return new Vector(currX - prevX, currY - prevY);
}

function killShot(npc: any) {
  const dieLikeStal = ["hydra", "mad-face", "scrap-face"];
  const npcName = dieLikeStal.includes(npc.id) ? "stal" : npc.id;
  const img = npc.getElementsByTagName("img");

  if (npc && img.length) {
    img[0].src = `img/${npcName}-collapse.gif`;
    setTimeout(() => {
      img[0].src = `img/${npcName}-collapse-end.png`;
    }, 500);
    setTimeout(() => {
      npc.remove();
    }, 1500);
  }
}
