import React, { useEffect } from "react";
import { NpcAction } from "./types";
import { animateNpc } from "./animation";
import standing from "./img/madFace.png";
import dying from "./img/stal-collapse.gif";

const ACTION_MAP = new Map([
  [NpcAction.Standing, standing],
  [NpcAction.Dying, dying]
]);

interface Props {
  moving: boolean;
}

function getSprite(action: NpcAction) {
  return ACTION_MAP.get(action) || standing;
}

export default (props: Props) => {
  const action = props.moving ? NpcAction.Moving : NpcAction.Standing;

  useEffect(() => {
    animateNpc(document.getElementById("spider"));
  }, []);

  return (
    <div id="mad-face" style={styles.sprite}>
      <img src={getSprite(action)} width="175" alt="sprite" />
    </div>
  );
};

const styles = {
  sprite: {
    position: "absolute",
    top: 16,
    right: 256,
    zIndex: 1
  }
};
