import React from "react";
import Player from "./Player";
import Stal from "./Stal";
import Spider from "./Spider";
import MadFace from "./MadFace";
import ScrapFace from "./ScrapFace";
import Zombie from "./Zombie";
import Hydra from "./Hydra";
import { handleClick } from "./animation";
import ground from "./img/ground.png";
import { MIN_VIEW_WIDTH } from "./constants";

interface Props {
  moving: boolean;
}

function animateScene(shouldAnimate: boolean) {
  return shouldAnimate ? "animatedBackground 1s linear infinite" : "";
}

export default (props: Props) => {
  const { moving } = props;
  const animation = animateScene(moving);

  return (
    <div
      style={styles.viewport}
      onClick={handleClick}
      onMouseDown={(ev: React.MouseEvent) => {
        ev.preventDefault();
      }}
      id="viewport"
    >
      <div style={styles.game}>
        <Player moving={moving} />
        <Stal moving={moving} />
        <Spider moving={moving} />
        <MadFace moving={moving} />
        <ScrapFace moving={moving} />
        <Zombie moving={moving} />
        <Hydra moving={moving} />
        <div id="hill" style={{ ...styles.hill }} />
        <div id="ground" style={{ ...styles.ground, animation }} />
      </div>
    </div>
  );
};

const styles = {
  game: {
    position: "relative",
    height: "100%",
    width: "100%"
  },
  ground: {
    backgroundImage: `url("${ground}")`,
    backgroundPosition: "0 0",
    backgroundSize: "21%",
    bottom: 0,
    height: 100,
    position: "absolute",
    width: "100%"
  },
  hill: {
    backgroundImage: `url("${ground}")`,
    backgroundPosition: "0 0",
    bottom: 100,
    position: "absolute",
    right: 0,
    height: 125,
    width: 250,
    backgroundSize: "100%"
  },
  viewport: {
    alignItems: "flex-end",
    backgroundColor: "#3c62af",
    boxSizing: "border-box",
    display: "flex",
    // height: window.innerHeight * 0.7,
    height: 641,
    margin: "auto",
    overflow: "hidden",
    // width: window.innerWidth * 0.7,
    width: 1176,
    minWidth: MIN_VIEW_WIDTH
  }
};
