import {
  MON_LIST_LOADING,
  MON_LIST_LOADED,
  MON_LIST_ERROR
} from './actionCreators';

const initialState = {
  monList: [],
  error: null,
  loading: false
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case MON_LIST_LOADING:
      return {
        ...state,
        error: null,
        loading: true
      };
    case MON_LIST_LOADED:
      return {
        ...state,
        monList: action.payload.results,
        error: null,
        loading: false
      };
    case MON_LIST_ERROR:
      return {
        ...state,
        error: action.payload,
        loading: false
      };
    default:
      return state;
  }
}
